.employeeinfodiv section{
    padding:20px;
    padding-top: 0px;
    border-top: 5px solid rgb(47, 47, 161);
    margin: 20px;
    margin-top: 0;
    background-color:white;
    box-shadow: 1px 5px 10px #0000005b;
}
.employeeinfodiv > h1{
padding-left:20px;

}
.employeeinfodiv label{
    font-weight:bold;
    display: inline;
    margin-bottom:20px;

}
.employeeinfodiv section > h1 > button{
    float: right;

}

.sectionmenu{
    display:flex;
    flex-direction:row;
    margin:0;
    padding-left:20px;
}
.sectionmenu button{
    padding:14px;
    margin:0;
    border:none;
    border-right:1px solid rgb(209, 209, 209);
    background-color:rgb(248, 248, 248);
    cursor:pointer;
}
.popup{
    position:fixed;
    width:100vw;
    left: 0vw;
    top:0vw;
    height:100vh;
    
    background-color:rgba(0, 0, 0, 0.679);
}

.popup > section{
    margin: 0 auto;
    margin-top:10vh;
    width: 40vw;
    border-top:none;
    padding-top:10px;

}